.h-container {
  width: 100%;
  min-width: 480px;
  height: auto;
  background: #84a178;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.ice-icon {
  width: 40px;
  height: 37px;
  background: red;
}

.menu-toggle svg {
  width: 32px;
  height: 32px;
  background: red;
}

.head-navigation {
  display: none;
}

.nav-list {
  flex-direction: column;
  gap: 28px;
  list-style: none;
  display: flex;
}

.nav-list_element {
  letter-spacing: 0;
  text-align: center;
  color: #fcfcfc;
  font-family: Montserrat;
  font-size: 32px;
  font-weight: 700;
  line-height: 39px;
}

.purchase_button {
  width: 152px;
  height: 42px;
  text-align: center;
  background: #f8d24e;
  border-radius: 10px;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 600;
  line-height: 17px;
}

@media screen and (min-width: 480px) and (max-width: 1199px) {
  .mobile {
    display: none;
  }
}

@media screen and (min-width: 1200px) {
  .desktop {
    display: none;
  }
}

.menu-container {
  width: 100%;
  height: auto;
  z-index: 999;
  background-color: #84a178;
  padding: 0 40px;
  transition: transform .25s ease-in-out, -webkit-transform .25s ease-in-out;
  position: fixed;
  top: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}





.advantages-section {
  background-color: #84a178;
  padding-bottom: 81px;
}

.advantages-style {
  flex-direction: column;
  align-items: center;
  gap: 16px;
  display: inline-flex;
}

.svg-advantages {
  width: 50px;
  height: 50px;
  background: #f8d24e;
  border-radius: 25px;
  justify-content: center;
  align-items: center;
  margin-top: 32px;
  display: flex;
}

.advantages-numbers {
  color: #fcfcfc;
  letter-spacing: 1.44px;
  text-transform: uppercase;
  text-align: center;
  font-family: Montserrat;
  font-size: 36px;
  font-weight: 700;
  line-height: normal;
}

.advantages-text {
  color: #fcfcfc;
  text-align: center;
  width: 230px;
  height: 40px;
  font-family: Montserrat;
  font-size: 12px;
  font-weight: 400;
  line-height: 170%;
}





.send-form-button {
  background-color: var(--default-btn-color);
  cursor: pointer;
  text-align: center;
  border: none;
  border-radius: 10px;
  align-self: center;
  padding: 12px 34px;
  font-family: Montserrat;
  font-size: 12px;
  font-weight: 600;
  line-height: 30px;
}

.backdrop {
  width: 100%;
  height: 100%;
  pointer-events: all;
  visibility: visible;
  background-color: #15142080;
  padding: 20px;
  transition: opacity .25s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.francise-modal {
  width: 100%;
  height: auto;
  background-color: var(--light-bg-font-color);
  border: 2px solid var(--default-btn-color);
  border-radius: 10px;
  margin: auto;
  padding: 28px;
  position: relative;
}

.close-modal-button {
  width: 30px;
  height: 30px;
  background-color: var(--light-bg-font-color);
  fill: var(--black-color);
  cursor: pointer;
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  inset: 6px 6px auto auto;
}

.is-hidden {
  pointer-events: none;
  visibility: none;
  opacity: 0;
}

.francise-form {
  flex-direction: column;
  display: flex;
}

.francise-form > h4 {
  text-align: center;
  font-family: Seymour One;
  line-height: 24px;
}

.francise-form > p {
  padding: 20px 0;
  font-family: Montserrat;
  font-size: 14px;
  line-height: 140%;
}

.inputs-container {
  flex-direction: column;
  display: flex;
}

label {
  margin-bottom: 10px;
  font-size: 12px;
}

.basic-input {
  width: 100%;
  height: 42px;
  border: 1px solid var(--main-bg-color);
  background: #f8d24e1a;
  border-radius: 10px;
  padding: 11px 12px;
}

.basic-input:focus {
  outline: none;
}

textarea {
  width: 100%;
  min-height: 120px;
  border: 1px solid var(--main-bg-color);
  resize: none;
  background: #f8d24e1a;
  border-radius: 10px;
  outline: none;
  padding: 12px 16px;
}



:root {
  --main-bg-color: #84a178;
  --light-bg-font-color: #fcfcfc;
  --dark-font-color: #131313;
  --default-btn-color: #f8d24e;
  --hover-btn-color: #0a8817;
  --disabled-btn-color: #ababab;
  --ice-cream-bg-color: #aec6a5;
  --ice-coffee-bg-color: #efd478;
  --milkshakes-bg-color: #d6936d;
  --footer-font-color: #7a7a7a;
  --modal-input-bg-color: #f8d24e1a;
  --white-color: #fff;
  --black-color: #000;
}

body {
  color: var(--dark-font-color);
  font-family: Montserrat, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
}

ul, ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  max-width: 480px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: 1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
}

